@font-face {
  font-family: 'Pelo700';
  src: url('./assets/PelotareSans-700wt.otf') format('opentype');
}

@font-face {
  font-family: 'Pelo400';
  src: url('./assets/PelotareSans-400wt.otf') format('opentype');
}

p {margin: 0}

body {
  background: linear-gradient(#FF009B, #EA52B4);
  color: #fff;
}

h1 { font-size: 30px; }
h4 { font-size: 20px; font-weight: normal; margin: 0}

.App {
  text-align: center;

  img { margin-bottom: 30px;}
}

.container {
  padding: 0px 20px 80px;
}

.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 40px;
}

.loader {
  width: 100vw;
  height: 100vh;
  position: relative;

  div { 
    position: absolute;
    top: calc(50% - 100px);
    left: calc(50% - 50px);
  }

  img {
    position: absolute;
    top: calc(50% - 160px);
    left: calc(50% - 145px);
  }
}

.overlay {
  background-color: #000;
  opacity: 0.7;
  width: 100vw;
  height: 100vh;
  position: absolute;
  z-index: 1;
}

.exit {
    position: absolute;
    right: 20px;
    top: 20px;
    z-index: 2;

    img {
      width: 30px;
    }
}

.modal {
  display: none;
}

.showModal {
  display: block;
}

@keyframes logoanim {
  from {
    transform: scale(0);
    opacity: 0;
  }
  to {
    transform: scale(0.7);
    opacity: 1;
  }
}

.logo {
  animation-name: logoanim;
  animation-duration: 1.2s;
  transform: scale(0.7);
  animation-timing-function: ease-in;
}
